.card {
     border: 4px rgb(99, 99,99) solid;
     border-radius: 2px;
     width: 150px;
     height: 150px;
     margin: 1rem;
     position: relative;
     box-shadow: 4px 4px 10px rgb(56, 56, 56);
     transition: all .2s ease-in-out;
}
.card > .img-container {
     height: 100%;
     overflow: hidden;
     background: rgb(255, 255, 255);
}
.card > .img-container > img {
     height: 100%;
     width: 100%;
}
.card:hover {
     box-shadow: 8px 8px 20px rgb(56, 56, 56);
     -webkit-transform: scale(1.1);
             transform: scale(1.1);
     cursor: pointer;
}
.card:active {
     -webkit-transform: translate(1px, 1px);
             transform: translate(1px, 1px);
}
nav {
     width: 100%;
     font-size: 3rem;
     color: #fff;
     margin-bottom: 50px;
     background-color:rgb(56, 56, 56);
     height: auto;
     font-family: 'Roboto', sans-serif;
}
a {
     color: #ED1D24;
}
a:hover {
     color: #ED1D24;
     text-decoration: none;
}
nav li {
     display: inline-block;
     padding: 26px 0;
     width: 25%;
}

ul {
     display: flex;
     align-items: center;
}
@media only screen and (max-width: 750px) {
     #cur-sco, #top-sco {
          font-size: 2rem;
     }
}
.wrapper { 
     background: rgb(175, 175, 175);
     background-image: url("https://cdn.allwallpaper.in/wallpapers/1920x1080/6733/comics-spider-man-marvel-1920x1080-wallpaper.jpg");
     height: 100%;
     display: flex;
     flex-flow: row wrap;
     justify-content: space-around;
     align-content: flex-start;
     overflow: auto;
     padding-bottom: 50px;
}
.title {
     width: 100%;
     text-align: right;
     font-size: 2rem;
     color: #444;
     margin-bottom: 50px;
     padding-left: 3%;
     padding-right: 3%;
}
.title {
     width:100%;
     text-align: center;
     margin-bottom: 50px;
}
* {
     box-sizing: border-box;
     font-family: 'Roboto', sans-serif;
}

html, body, #root {
     padding: 0;
     margin: 0;
     height: 100%;
}
